@import "Calculator/assets/fonts/digit/index.css";

html, body {
  height: 100%;
  min-height: 100%;
}

body {
  background: -webkit-gradient(linear,left top, left bottom,color-stop(50%, #000),to(#222));
  background: -webkit-linear-gradient(top,#000 50%,#222 100%);
  background: -o-linear-gradient(top,#000 50%,#222 100%);
  background: linear-gradient(to bottom,#000 50%,#222 100%);
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}



